/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faEnvelopeOpen } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { graphql, useStaticQuery } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import { breakpoints, colors, fonts } from './theme';

const centerText = css`
  text-align: center;
  font-family: ${fonts.primary};
`

const bar = css`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  left: 0;
  top: 0;
  padding: 20px 10px;
  font-family: ${fonts.primary};
  background-color: white;
  box-sizing: border-box;

  position: fixed;
  height: 100%;
  width: 240px;
  @media (max-width: ${breakpoints[0]}px) {
    position: relative;
    width: 100%;
    height: auto;
  }
`

const authorName = css`
  font-family: ${fonts.secondary};
  font-weight: 700;
  text-transform: uppercase;
  margin: 0 0 10px;
  position: relative;

  @media (min-width: ${breakpoints[0]}px) {
    padding-bottom: 15px;
    &::after {
      content: "";
      background-color: ${colors.primary};
      position: absolute;
      display: block;
      width: 7px;
      height: 7px;
      border-radius: 100%;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
    }
  }
`

const AvatarStyle = css`
  border-radius: 50%;
  height: 140px;
  width: 140px;
  margin: 0 auto 10px;
  overflow: hidden;
  transition: transform 0.35s;
  &:hover {
    transform: scale3d(0.9, 0.9, 1);
  }
`

const Sidebar = () => {

  const data = useStaticQuery(graphql`
  {
    allImageSharp(filter: {id: {eq: "66aa61ed-1080-55fc-a5c3-c1f5eab49ee2"}}) {
      edges {
        node {
          id
          gatsbyImageData
        }
      }
    }
    site {
      siteMetadata {
        title
        authorName
        social {
          github
          mail
          linkedin
        }
      }
    }
  }
  `);

  return (
  <div css={bar}>
    <div css={css`
      text-align: center;
      margin: 0 0 40px;
      @media (max-width: ${breakpoints[0]}px) {
        margin: 0;
      }
    `}>
      <StaticImage
        css={AvatarStyle} alt="profile picture" src="../images/profile.jpg"
      />
      <div css={authorName}>
        {data.site.siteMetadata.authorName}
      </div>
      <p>I am a developer focusing on ML and data engineering. Always hungry to keep learning.</p>
    </div>
    <div css={centerText}>
      <section>
        <h3 css={css`
          text-transform: uppercase;
          font-size: 12px;
          position: relative;
          &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 50%;
            width: calc(50% - 48px);
            transform: translateY(-50%);
            background-color: ${colors.primary};
            height: 2px;
          }
          &::after {
            content: "";
            position: absolute;
            right: 0;
            top: 50%;
            width: calc(50% - 48px);
            transform: translateY(-50%);
            background-color: ${colors.primary};
            height: 2px;
          }
          @media (max-width: ${breakpoints[0]}px) {
            display: None;
          }
        `}>Contact me</h3>
        <ul css={css`
          list-style: none;
          padding-inline-start: 0px;
          li {
            display: inline-block;
          }
          a {
            padding: 10px;
            color: ${colors.primary};
          }
        `}>
          {data.site.siteMetadata.social.github &&
          <li>
            <a href={data.site.siteMetadata.social.github}>
              <FontAwesomeIcon icon={faGithub} />
            </a>
          </li>}
          {data.site.siteMetadata.social.linkedin &&
          <li>
            <a href={data.site.siteMetadata.social.linkedin}>
              <FontAwesomeIcon icon={faLinkedin} />
            </a>
          </li>}
          {data.site.siteMetadata.social.mail &&
          <li>
            <a href={`mailto:${data.site.siteMetadata.social.mail}`}>
              <FontAwesomeIcon icon={faEnvelopeOpen} />
            </a>
          </li>}
        </ul>
      </section>
      <div>
        <p css={css`
          @media (max-width: ${breakpoints[0]}px) {
            display: None;
          }
        `}>{new Date().getFullYear()} &copy; {data.site.siteMetadata.authorName}</p>
      </div>
    </div>
  </div>
  )
}

export default Sidebar